








































































































import { QuizEventModelv4, State } from '@/apis/models/QuizEventModel';
import { MessageEventBus } from '@/libs/EventBus';
import Vue, { PropType } from 'vue';

export default Vue.extend({
  components: {
  },
  props: {
    quizEvent: {
      type: Object as PropType<QuizEventModelv4 | null>,
      required: true,
    },
    dateTime: {
      type: Object,
      default: () => ({
        startValid: '',
      }),
    },
    isLocked: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      nowTimestamp: Math.floor(new Date(Math.floor(Date.now())).getTime() / 1000), // 10位數
      startValidDate: null as null | string,
      startValidTime: null as null | string,
      endValidDate: null as null | string,
      endValidTime: null as null | string,
      scheduleSendDate: null as null | string,
      scheduleSandTime: null as null | string,
      copyMessageDisplay: false,
      copyMessageTimeoutId: 0,
    };
  },
  computed: {
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    quizEventId(): string {
      return this.$store.state.currentQuizEvent.uuid;
    },
  },
  watch: {
    startValidDate() {
      this.saveDateTime();
    },
    startValidTime() {
      this.saveDateTime();
    },
    endValidDate() {
      this.saveDateTime();
    },
    endValidTime() {
      this.saveDateTime();
    },
    scheduleSendDate() {
      this.saveDateTime();
    },
    scheduleSandTime() {
      this.saveDateTime();
    },
    quizEvent() {
      this.initDefaultDate();
    },
  },
  async mounted() {
    MessageEventBus.$on('clear-copied-message', this.clearCopyMessageTimeout);
    this.initDefaultDate();
  },
  methods: {
    remindValidTime() {
      if (this.startValidDate && this.startValidTime && this.endValidDate && this.endValidTime) {
        // console.log(this.startValidDate, this.startValidTime, this.isoDateTimeToTimestamp(this.dateTime.startValid));
        // console.log(this.endValidDate, this.endValidTime, this.isoDateTimeToTimestamp(this.dateTime.endValid));
        const startValid = this.isoDateTimeToTimestamp(this.dateTime.startValid);
        const endValid = this.isoDateTimeToTimestamp(this.dateTime.endValid);
        if (startValid > endValid) {
          console.log('必須要大於起始日');
          this.dateTime.validTimeRemind = '小提醒： 結束時間需大於開始時間';
        } else {
          this.dateTime.validTimeRemind = '';
        }
      }
    },
    remindScheduledTime() {
      if (this.scheduleSendDate && this.scheduleSandTime && this.endValidDate && this.endValidTime) {
        const scheduledTime = this.isoDateTimeToTimestamp(this.dateTime.scheduledTime);
        const endValid = this.isoDateTimeToTimestamp(this.dateTime.endValid);
        if (scheduledTime > endValid) {
          this.dateTime.sandTimeRemind = '小提醒： 發送時間需在結束時間之前';
        } else {
          this.dateTime.sandTimeRemind = '';
        }
      }
    },
    clearDateTime() {
      this.startValidDate = '';
      this.startValidTime = '';
      this.endValidDate = '';
      this.endValidTime = '';
    },
    clearScheduleMode() {
      this.scheduleSendDate = '';
      this.scheduleSandTime = '';
    },
    initDefaultDate() {
      if (!this.quizEvent) {
        throw new Error('quiz event not ready');
      }

      if (this.quizEvent.startTime) {
        const timestamps = this.isoDateTimeToTimestamp(this.quizEvent.startTime);
        const object = this.toDateStringObject(timestamps / 1000);
        this.startValidDate = object.date;
        this.startValidTime = object.time;
      }
      if (this.quizEvent.endTime) {
        const timestamps = this.isoDateTimeToTimestamp(this.quizEvent.endTime);
        const object = this.toDateStringObject(timestamps / 1000);
        this.endValidDate = object.date;
        this.endValidTime = object.time;
      }
      if (this.quizEvent.stateHistory) {
        const index = this.quizEvent.stateHistory.length - 1;
        switch (this.quizEvent.stateHistory[index].state) {
          case State.SCHEDULED:
          case State.DONE:
            if (this.quizEvent.scheduledTime) {
              console.log(this.quizEvent.scheduledTime);
              const timestamps = this.isoDateTimeToTimestamp(this.quizEvent.scheduledTime);
              const object = this.toDateStringObject(timestamps / 1000);
              this.scheduleSendDate = object.date;
              this.scheduleSandTime = object.time;
            }
            break;
          case State.CANCELED:
            this.scheduleSendDate = null;
            this.scheduleSandTime = null;
            break;
          default:
            console.log('default:', this.quizEvent.stateHistory[index].state);
            break;
        }
      } else {
        console.log('quizEvent.stateHistory 不存在');
      }
    },
    saveDateTime() {
      if (this.startValidDate && this.startValidTime) {
        this.dateTime.startValid = this.toIsoDateTime(this.startValidDate, this.startValidTime);
      } else {
        this.dateTime.startValid = null;
      }
      if (this.endValidDate && this.endValidTime) {
        this.dateTime.endValid = this.toIsoDateTime(this.endValidDate, this.endValidTime);
      } else {
        this.dateTime.endValid = null;
      }
      if (this.scheduleSendDate && this.scheduleSandTime) {
        this.dateTime.scheduledTime = this.toIsoDateTime(this.scheduleSendDate, this.scheduleSandTime);
      } else {
        this.dateTime.scheduledTime = null;
      }
    },
    isoDateTimeToTimestamp(isoDateTime: string) {
      const timestamp = Date.parse(isoDateTime);
      return timestamp;
    },
    toDateStringObject(timestamp: number) {
      const dateString = this.toDateStringFormat(timestamp);
      const dateStringArray = dateString.split(' ', 2);

      const object = {
        date: dateStringArray[0],
        time: dateStringArray[1],
      };
      return object;
    },
    toIsoDateTime(date: string, time: string) {
      const timestamp = this.dateStringsToTimestamp(date, time);
      const isoDateTime = new Date(timestamp).toISOString();
      return isoDateTime;
    },

    // 工具-------------------------
    toDateStringFormat(timestamp: number) { // [公式]把時間戳換成字串
      const date = new Date(timestamp * 1000);
      const year = date.getFullYear();
      const month = date.getMonth() + 1;
      const day = date.getDate();
      const hour = date.getHours();
      const min = date.getMinutes();

      let addzeromonth = '';
      let addzeroday = '';
      let addzerohour = '';
      let addzeromin = '';

      if (month < 10) {
        addzeromonth = `0${month}`;
      } else {
        addzeromonth = `${month}`;
      }
      if (day < 10) {
        addzeroday = `0${day}`;
      } else {
        addzeroday = `${day}`;
      }
      if (hour < 10) {
        addzerohour = `0${hour}`;
      } else {
        addzerohour = `${hour}`;
      }
      if (min < 10) {
        addzeromin = `0${min}`;
      } else {
        addzeromin = `${min}`;
      }
      const currentDateTime = `${String(year)}-${addzeromonth}-${addzeroday} ${addzerohour}:${addzeromin}`;
      return currentDateTime;
    },
    dateStringsToTimestamp(date: string, time: string) { // [工具]change complete date strings to timestamp
      const completeDateString = `${date}T${time}`;
      const timestamp = (new Date(completeDateString)).getTime();
      return timestamp;
    },

    // sendmode---------
    copyEventCode() {
      if (!this.quizEvent) {
        throw new Error('quiz event not ready');
      }

      MessageEventBus.$emit('clear-copied-message');
      // eslint-disable-next-line @typescript-eslint/no-unused-vars
      this.$copyText(this.quizEvent.shortId).then((e) => {
        this.copyMessageDisplay = true;
        this.copyMessageTimeoutId = setTimeout(this.clearCopyMessage, 1500);
      }, (e) => {
        console.error('複製失败', e.text);
      });
    },
    clearCopyMessage() {
      this.copyMessageDisplay = false;
    },
    clearCopyMessageTimeout() {
      this.clearCopyMessage();
      clearTimeout(this.copyMessageTimeoutId);
    },
    manualModeUpdate() {
      this.$emit('manualModeUpdate');
    },
  },
});
