










































































































import Vue from 'vue';
import QuizEventApi from '@/apis/QuizEventApi';
import { QuizEventModelv4 } from '@/apis/models/QuizEventModel';
import checkedItems from '@/components/test_maker/CheckedItems.vue';
import eventSetting from '@/components/test_maker/EventSetting.vue';
import { MessageEventBus } from '@/libs/EventBus';
import Auth from '@/libs/auth';
import SelectQuizGroupModal from '@/components/test_maker/SelectQuizGroupModal.vue';
import SelectMemberGroupModal from '@/components/test_maker/SelectMemberGroupModal.vue';
import { WorkspaceObjectModel, WorkspaceObjectType } from '@/apis/models/WorkspaceObjectModel';
import { WorkspaceSoftLinkModel } from '@/apis/models/WorkspaceSoftLinkModel';
import BackButton from '@/components/BackButton.vue';

export default Vue.extend({
  components: {
    // checkList,
    checkedItems,
    eventSetting,
    SelectQuizGroupModal,
    SelectMemberGroupModal,
    BackButton,
  },
  data() {
    return {
      quizEvent: null as QuizEventModelv4 | null, // 本[測驗]的資訊
      isLocked: false,
      saved: '',
      monitor: -1,
      nowTimestamp: Math.floor(new Date(Math.floor(Date.now())).getTime() / 1000), // 10位數
      memberGroupsInfo: {
        name: 'member',
        checkedList: [] as WorkspaceObjectModel[], // 這是已選的[班級]
      },
      quizGroupsInfo: {
        name: 'quiz',
        checkedList: [] as WorkspaceObjectModel[], // 這是已選的[題組]
      },
      dateTime: {
        startValid: '',
        endValid: '',
        scheduledTime: '',
        defaultdEndTime: '',
        validTimeRemind: '',
        sandTimeRemind: '',
        endTimeRemind: '',
      },
      saveSignalDisplay: false,
      saveSignalTimeoutId: 0,
    };
  },
  computed: {
    isLoading(): boolean {
      return this.$store.state.isLoading;
    },
    workspaceId(): string {
      return this.$store.state.currentWorkspace.uuid;
    },
    quizEventId(): string {
      return this.$store.state.currentQuizEvent.uuid;
    },
  },
  async mounted() {
    Auth.loginCheck();
    this.$store.commit('updateLoading', true);
    MessageEventBus.$on('clear-copied-message', this.clearSaveSignalTimeout);
    this.startStateMonitor();
    try {
      await QuizEventApi.getQuizEvent(this.quizEventId).then((quizEvent) => {
        this.quizEvent = quizEvent;
        this.isLocked = quizEvent.isLocked;
      });
    } finally {
      this.$store.commit('updateLoading', false);
    }
  },
  beforeDestroy() {
    clearInterval(this.monitor);
  },
  methods: {
    startStateMonitor() {
      if (this.monitor !== -1) {
        clearInterval(this.monitor);
      }
      this.monitor = setInterval(async () => {
        QuizEventApi.getQuizEventState(this.quizEventId).then((state) => {
          this.isLocked = state.isLocked;
        });
      }, 10 * 1000);
    },
    updateEvent() {
      if (!this.quizEvent) {
        throw new Error('quiz event not ready');
      }

      this.$store.commit('updateLoading', true);
      this.quizEvent.startTime = this.dateTime.startValid;
      this.quizEvent.endTime = this.dateTime.endValid;
      this.quizEvent.scheduledTime = this.dateTime.scheduledTime;

      QuizEventApi.update(this.quizEventId, this.quizEvent)
        .then(() => {
          this.saveEventSignal();
        })
        .catch(
          (error) => {
            console.error('update儲存出錯囉', error);
          },
        )
        .finally(() => {
          this.$store.commit('updateLoading', false);
        });
    },
    manualModeUpdate() {
      if (!this.quizEvent) {
        return;
      }

      this.$store.commit('updateLoading', true);
      this.quizEvent.startTime = this.dateTime.startValid;
      this.quizEvent.endTime = this.dateTime.endValid;
      this.quizEvent.scheduledTime = null;
      // if (Date.parse(this.dateTime.endValid) > Date.parse(this.dateTime.startValid)) {
      this.dateTime.validTimeRemind = '';
      QuizEventApi.update(this.quizEventId, this.quizEvent).then(() => {
        this.saveEventSignal();
        QuizEventApi.sendNow(this.quizEventId).then(() => {
          clearInterval(this.monitor);
          this.isLocked = true;
        }).catch(
          (error) => {
            console.log('quiz event send now 出錯囉', error);
          },
        ).finally(() => {
          this.$store.commit('updateLoading', false);
        });
      });
    },
    saveEventSignal() {
      MessageEventBus.$emit('clear-copied-message'); // 借用這一個eventbus
      this.saveSignalDisplay = true;
      this.saveSignalTimeoutId = setTimeout(this.clearSaveSignal, 1500);
    },
    clearSaveSignal() {
      this.saveSignalDisplay = false;
    },
    clearSaveSignalTimeout() {
      this.clearSaveSignal();
      clearTimeout(this.saveSignalTimeoutId);
    },
    openSelectMemberGroupModal() {
      this.$modal.show('select-member-group-modal', {
        defaultSelection: this.quizEvent?.memberGroups,
      });
    },
    updateSelectedMemberGroup(selection: WorkspaceObjectModel[]) {
      if (!this.quizEvent) {
        throw new Error('quiz event not ready');
      }

      this.quizEvent.memberGroups = selection;
    },
    openSelectQuizGroupModal() {
      this.$modal.show('select-quiz-group-modal', {
        defaultSelection: this.quizEvent?.quizGroups,
      });
    },
    updateSelectedQuizGroup(selection: WorkspaceObjectModel[]) {
      if (!this.quizEvent) {
        throw new Error('quiz event not ready');
      }

      if (selection.length > 1) {
        // TODO: quiz group only accept single selection, handle many selection
      }

      if (selection.length === 1) {
        if (selection[0].metadata.type === WorkspaceObjectType.SoftLink
          && (selection[0] as WorkspaceSoftLinkModel).sourceObject.metadata.type !== WorkspaceObjectType.QuizGroup) {
          // TODO: handle wrong selection type
        } else if (selection[0].metadata.type !== WorkspaceObjectType.QuizGroup) {
          // TODO: handle wrong selection type
        }
      }

      this.quizEvent.quizGroups = selection;
    },
  },
});
